@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
}

.colorBackground {
  background-color: #ccedfa;
  top: 8%;
  position: absolute;
  height: auto;
  width: auto;
  min-width: 100%;
}

.img {
  height: 150px;
  width: 150px;
}

.img2 {
  margin-left: 50px;
  height: 150px;
  width: 150px;
}

.imgAccueil {
  height: 320px;
  width: 320px;
}

.map {
  height: auto;
  max-width: 800px;
  border: solid;
}

.font {
  font-size: 400%;
}

.bar {
  border-bottom: solid;
  width: 100%;
  position: relative;
  border-width: 10px;
  border-bottom-color: #6e79a0;
}

@media all and (max-width: 760px) {
  .font {
    font-size: 250%;
  }
}
